header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 12; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300;
		&:hover {
			text-decoration: none;
			color: $main-color; } }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }
.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0,0,0, .6);
	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }
	.subheader_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }
	.subheader_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }
	.subheader_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }


.promo {
	min-height: 800px;
	background: url('../img/uber_bg.png') center center/cover no-repeat;
	padding: 150px 0 135px 0;
	.promo_header {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	.promo_subheader {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	.promo_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	.promo_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase; } }
.reasons {
	padding: 71px 0 65px 0; }
.label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center; }
.title {
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }

.reasons {
	&_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }
	&_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 26px;
			width: 113px;
			height: 113px;
			background-color: #1eacc7;
			border-radius: 100%; }
	.reasons_descr {
			width: 343px;
			.reasons_subtitle {
				line-height: 21px;
				color: #1eacc7;
				font-size: 17px;
				font-weight: 400; }
			.reasons_text {
				color: #202020;
				font-size: 15px;
				font-weight: 300;
				line-height: 21px; } } }
.mobile {
	min-height: 860px;
	padding: 48px 0 68px 0;
	background: url('../img/mobile_bg.png');
	padding-top: 92px;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/card1.png'); }
		&_2 {
			background: url('../img/card2.png'); }
		&_3 {
			background: url('../img/card3.png'); }
		&_4 {
			background: url('../img/card4.png'); }
		&_5 {
			background: url('../img/card5.png'); }
		&_6 {
			background: url('../img/card6.png'); }
		&_subtitle {
			font-style: normal;
			font-weight: 400;
			font-size: 24px;
			line-height: 31px;
			align-items: center;
			text-align: center;
			text-transform: uppercase;
			color: #FFFFFF; }
		&_plus {
			display: flex;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			justify-content: center;
			align-items: center;
			position: absolute;
			width: 26px;
			height: 26px;
			background: #000;
			border-radius: 100%;
			padding: 6px;
			margin: 0 auto;
			cursor: pointer;
			&::before {
				content: '';
				display: block;
				width: 16px;
				height: 2px;
				background-color: $main-color; }
			&::after {
				content: '';
				display: block;
				width: 2px;
				height: 16px;
				background-color: $main-color;
				position: absolute;
				left: 50%;
				transform: translateX(-50%); }
			&:hover {
				&:after, &:before {
					background-color: $second-color;
					transition: 1s all; } } } } }


.subtitle {
	text-align: center; }


.choice {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
	&_img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		width: 100%;
		z-index: 2; }
	&_descr {
		text-align: center;
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px; }
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: 1; } }
.label {
	position: relative;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		background-color: #fff;
		color: #000; }
	&_min {
		width: 76px; } }

.title {
	position: relative;
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	line-height: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff; } }

.subtitle {
	position: relative;
	margin-top: 19px;
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff; } }

.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_npm {
			margin-bottom: 0px; } }
	&_title {
		font-weight: 300;
		font-size: 17px;
		color: #1CA8C3; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			margin-bottom: 10px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 13px;
				width: 20px;
				height: 20px;
				background: url('../img/ch1.png') center (center/cover) no-repeat;
				transform: translateY(-50%); } } }
	&_descr {
		color: #1CA8C3; }
	.warning {
		.require_title {
			color: $second-color; }
		.require_list {
			li {
				&::before {
					background: url('../img/ch2.png') center (center/cover) no-repeat; } } } } }

.world {
	position: relative;
	min-height: 717px;
	padding: 49px 0;
	background: url('../img/world_bg.png') center (center / cover) no-repeat;
	&_link {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		text-align: center;
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		color: #1fbad6;
		&:hover {
			text-decoration: none;
			color: #fff; } } }


footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }
.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } }

	&_mobile {} }
