@media (max-width: 1200px) {
	.menu {
		.menu_item {
			padding-right: 20px; }
		.menu_link {
			font-size: 13px; } }
	.promo {
		.promo_header {
			font-size: 50px; } }
	.reasons {
		.reasons_block {
			align-items: flex-start; }
		.reasons_descr {
			width: 300px; } } }

@media (max-width: 991px) {
	.menu {
		.menu_item {
			padding-right: 8px; }
		.menu_link {
			font-size: 10px; } }
	.subheader {
		.subheader_phone {
			font-size: 20px; }
		.subheader_btn {
			margin-top: 21px;
			height: 40px;
			width: 100%; } }
	.reasons {
		.reasons_block {
			flex-direction: column;
			align-items: center; }
		.reasons_descr {
			margin-top: 12px;
			width: 300px; } } }

